            // Send the POST request with custom headers
            const response = await axios.post('https://api.sobotdadeh.com/v1/auth/init', {}, {
                headers: {
                    'Api-Token': apiKey,
                    'Authorization': `Bearer ${token}`,
                    'IMEI': imei
                }
            });
