import './Blog.css'

// Images
import blogimg1 from './image/aboutimg2.webp'
import blogimg2 from './image/aboutimg3.webp'

// Icons 
import leftarrow from './Icons/leftarrowslider.svg'
import leftkey from './Icons/expandleft.svg'
import rightkey from './Icons/expandright.svg'
const Blog = () => {
    const Blogcards = ((props)=>{
        return(
                <div className="Blog-Cards-box">
                    <img src={props.img} alt="Blog image" />
                    <h1>{props.title}</h1>
                    <span><img src={leftarrow} alt="left arrow" /> </span>
                </div>
        )
    })
    return ( 
        <>
         <div className='header-about'>
                <h1></h1>
            </div>
            <div className='Blog-content'>
                <div className="Blog-content-back">
                    <div className='Blog-content-main'>
                        <div className="Blog-Header">
                            <h1> </h1>
                            <p>                                                                                                                                     .</p>
                            <img src={blogimg1} alt="Blog image" />
                            <p>                                                                                                                                     .</p>
                            <p>                                                                                                                                     .</p>
                        </div>
                        <div className="Blog-middle">
                            <img src={blogimg2} alt="Blog image" />
                            <p>                                                                                                                                     .</p>
                        </div>
                        <div className="Blog-end">
                            <p>                                                                                                                                     .</p>
                            <p>                                                                                                                                     .</p>
                        </div>
                        <h1 className='BlogCards-header'> </h1>
                        <div className="Blog-Cards">
                            <div className="Blog-Cards-arrows">
                                <img src={leftarrow} alt="left key" className='Blog-Cards-arrows-left'/>
                                <img src={rightkey} alt="left key" className='Blog-Cards-arrows-right'/>
                            </div>
                            <Blogcards
                                img={blogimg2}
                                title=' '
                            ></Blogcards>
                            <Blogcards
                                img={blogimg2}
                                title=' '
                            ></Blogcards>
                            <Blogcards
                                img={blogimg2}
                                title=' '
                            ></Blogcards>
                            <Blogcards
                                img={blogimg2}
                                title=' '
                            ></Blogcards>
                        </div>
                    </div>
                </div>
            </div>
        </>
     );
}
 
export default Blog;